.fade-enter {
    opacity: 1;
}

.fade-enter-active {
    -webkit-animation: show 0.5s !important;
            animation: show 0.5s !important;
}

.fade-leave {
    opacity: 0 !important;
}

.fade-leave-active {
    opacity: 0.01;
    -webkit-animation: fades 0.5s ease-out !important;
            animation: fades 0.5s ease-out !important;
}

.fade-appear {
    opacity: 0.01;
    -webkit-animation: show 0.5s ease-out !important;
            animation: show 0.5s ease-out !important;
}

.fade-appear-active {
    opacity: 1 !important;
}

.show_rate {
    -webkit-animation: show 0.5s ease-in !important;
            animation: show 0.5s ease-in !important;
}
.show_rate1{
    -webkit-animation: fades 0.5s ease-out !important;
            animation: fades 0.5s ease-out !important;
}


@-webkit-keyframes show {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}


@keyframes show {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@-webkit-keyframes fades {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}

@keyframes fades {
    0% {
        opacity: 1;
    }

    100% {
        opacity: 0;
    }
}
